import { NavLink } from "react-router-dom";
import resume from "../../images/.pdf";
import "./Nav.scss";

export default function Nav() {
  return (
    <nav className="nav">
      <ul>
        <li>
          <NavLink to="/"></NavLink>
        </li>
        <li>
          <NavLink to="/projects"></NavLink>
        </li>
        <li>
          <a href={resume} target="blank">
            
          </a>
        </li>
      </ul>
    </nav>
  );
}
